export const scrollToElement = (elementSelector, offset, withHeaderOffset) => {
  const element = document.querySelector(elementSelector);
  if (!element) return;

  const { top } = element.getBoundingClientRect();

  let headerOffset = 0;

  if (withHeaderOffset) {
    headerOffset =
      window.cssVariables['header-height'] +
      window.cssVariables['announcement-banner-height'];
  }

  scrollY = top + window.pageYOffset - headerOffset + 10 + (offset ?? 0);

  window.scrollTo({
    top: scrollY,
    behavior: 'smooth',
  });
};

export function markScriptFullyLoaded(scriptTitle) {
  window.scriptLoadMap[scriptTitle] = true;
}

export function isScriptAdded(src) {
  return Boolean(document.querySelector(`script[src="${src}"]`));
}

export function isScriptFullyLoaded(scriptTitle) {
  return (
    window.scriptLoadMap?.hasOwnProperty(scriptTitle) &&
    window.scriptLoadMap[scriptTitle]
  );
}

export const checkIsMobile = () => window.innerWidth <= 749;

export const checkIsTablet = () => window.innerWidth < 1024;

export const addUniqueActiveClass = (children, index, className) => {
  children.forEach((el) => {
    el.classList.remove(className);
  });
  children[index].classList.add(className);
};
